.animA {
  position: absolute;
  top: 5px;
  z-index: 5;
}

.animB {
  position: absolute;
  z-index: 5;
  top: 60px;
}

.animC {
  z-index: 5;
  position: absolute;
  font-size: 3rem;
  left: 40px;
}

header {
  /* border: 1rem s; */
  margin-bottom: 1rem;
  min-height: 100px;
  /* box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
    rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
    rgba(255, 255, 255, 0.08) 0px 1px 0px inset; */
}

header button {
  background-color: rgba(0, 255, 85, 0.589);
  height: 2rem;
  width: 5rem;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  position: fixed;
  right: 1%;
  top: 1%;
  display: flex;
  justify-content: center;
}

header button:hover {
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

@media screen and (max-width: 330px) {
  header button {
    left: 1%;
    top: 0;
  }
}
